


































































































    declare global {
        interface Window{ PrismicJS: any, PrismicDOM: any }
    }	

    import { Component, Vue, Watch } from "vue-property-decorator";
	import { Route } from 'vue-router';
	import _Prismic from '@/prismic';
	
	Component.registerHooks(['beforeRouteEnter']);

	@Component
	export default class VideoStream extends Vue { 
	    prismicDOM = window.PrismicDOM;
        tracks = [];
        bibles= [];
			
		beforeRouteEnter(to: Route, from: Route, next: Function) {
			next((vm) => { vm._getPage(); });
		}

		async _getPage() {
			var api = await _Prismic();
			let response = (await api.query(window.PrismicJS.Predicates.at('document.type', 'media'),
				{ fetch:[ 'media.audio_books' ]}
			)).results[0].data;
			console.log(response);

            this.tracks = response.audio_books;
		}

		_scrollTo(id: string) {
			var el = document.getElementById(id);
			el.scrollIntoView({behavior: 'smooth'});
		}

		viewMedia(e) {

		}
}
